body {
  text-align: center;
  background-color: #f6f6f6; }

input {
  border-style: groove; }

button {
  border-style: groove; }

.shadow {
  box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.03); }

.chat-wrap {
  border: 1px solid #ced4da;
  border-radius: 5px; }
  .chat-wrap input:focus {
    outline: none; }
  .chat-wrap .chat-box {
    margin: 1rem;
    border: 1px solid #e9ecef;
    background: white;
    height: 45vh;
    border-radius: 0 5px 5px 0;
    overflow-y: auto; }
  .chat-wrap .chat-input-box {
    background: white;
    height: 50px;
    line-height: 50px;
    border: 1px solid #e9ecef;
    border-radius: 5px;
    margin: 0 1rem 1rem 1rem; }
    .chat-wrap .chat-input-box:hover {
      cursor: pointer; }
    .chat-wrap .chat-input-box input {
      width: 75%;
      border-style: none;
      font-size: 0.9rem;
      text-align: center; }
  .chat-wrap .btn-add-container {
    float: right;
    background: linear-gradient(to right, #6478fb, #8763fb);
    display: block;
    width: 3rem;
    border-radius: 0 5px 5px 0; }
    .chat-wrap .btn-add-container:hover {
      cursor: pointer; }
  .chat-wrap .btn-add {
    color: white;
    vertical-align: middle; }
