body {
  text-align: center;
  background-color: #f6f6f6; }

input {
  border-style: groove; }

button {
  border-style: groove; }

.shadow {
  box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.03); }

.chat-msg-wrap {
  width: 100%;
  display: flex;
  flex-flow: row wrap; }
  .chat-msg-wrap .chat-msg {
    margin: 1rem 1rem;
    padding: 0.1rem 1rem;
    max-width: 25rem;
    min-height: 3rem;
    border-radius: 1.5rem;
    display: flex;
    flex-flow: row wrap;
    justify-content: start;
    align-items: center; }
    .chat-msg-wrap .chat-msg span {
      width: 70%; }
    .chat-msg-wrap .chat-msg .chat-picture {
      width: 3rem;
      height: 3rem;
      border: 1px solid white;
      margin: 0.5rem 1rem 0.5rem -0.5rem;
      border-radius: 1.5rem; }
  .chat-msg-wrap.chat-right {
    justify-content: flex-end; }
    .chat-msg-wrap.chat-right div {
      color: white;
      background: #845ef7; }
  .chat-msg-wrap.chat-left {
    justify-content: flex-start; }
    .chat-msg-wrap.chat-left div {
      color: #495057;
      background: #dbe4ff; }
  .chat-msg-wrap .chat-nick {
    font-weight: bold;
    font-size: 0.7rem;
    margin-top: 0.3rem; }
