body {
  text-align: center;
  background-color: #f6f6f6; }

input {
  border-style: groove; }

button {
  border-style: groove; }

.shadow {
  box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.03); }

.hide {
  display: none; }

.show {
  display: block; }

.nicknm-gray-background {
  background: rgba(100, 100, 100, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 5; }

.nicknm-wrapper {
  z-index: 9999;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  background: #7950f2;
  height: 30%;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
  min-width: 18rem; }
  .nicknm-wrapper .nicknm-modal {
    height: 100%;
    width: 100%;
    background: #f1f3f5;
    padding: 1rem auto;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center; }
  .nicknm-wrapper .nicknm-input-container {
    width: 95%;
    line-height: 5rem;
    margin: 0 auto;
    border: 1px solid white;
    border-radius: 5px;
    background: white; }
    .nicknm-wrapper .nicknm-input-container:hover {
      cursor: text; }
    .nicknm-wrapper .nicknm-input-container input {
      border-style: none;
      font-size: 0.9rem;
      width: 70%;
      min-width: 10rem;
      text-align: center; }
      .nicknm-wrapper .nicknm-input-container input:focus {
        outline: none; }
    .nicknm-wrapper .nicknm-input-container .nicknm-btn-add {
      float: right;
      height: 5rem;
      background: linear-gradient(to right, #6478fb, #8763fb);
      display: block;
      width: 5rem;
      border-radius: 0 5px 5px 0; }
      .nicknm-wrapper .nicknm-input-container .nicknm-btn-add:hover {
        cursor: pointer; }
      .nicknm-wrapper .nicknm-input-container .nicknm-btn-add i {
        vertical-align: middle;
        color: white; }
